//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	emergencyfundReason:'',
	emergencyfundWay:'',
	noemergencyFundReason:'',
	inspirationForEmergencyFund:'',
	investments:''
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'stepfour':
		//console.log('got it')
		return {
			...state,
			emergencyfundReason:action.payload.emergencyfundReason,
			emergencyfundWay:action.payload.emergencyfundWay,
			investments:action.payload.investments,
			noemergencyFundReason:action.payload.noemergencyFundReason,
			inspirationForEmergencyFund:action.payload.inspirationForEmergencyFund
		}
		case 'reset':
		return {
			...state,
			emergencyfundReason:action.payload,
			emergencyfundWay:action.payload,
			investments:action.payload,
			noemergencyFundReason:action.payload,
			inspirationForEmergencyFund:action.payload
		}
		default :
		return state
	}
}