//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	investmentReason:'',
	investmentWay:'',
	noinvestmentReason:'',
	inspirationForInvestment:'',
	insurance:''
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'stepfive':
		//console.log('got it')
		return {
			...state,
			investmentReason:action.payload.investmentReason,
			investmentWay:action.payload.investmentWay,
			insurance:action.payload.insurance,
			noinvestmentReason:action.payload.noinvestmentReason,
			inspirationForInvestment:action.payload.inspirationForInvestment
		}
		case 'reset':
		return {
			...state,
			investmentReason:action.payload,
			investmentWay:action.payload,
			insurance:action.payload,
			noinvestmentReason:action.payload,
			inspirationForInvestment:action.payload
		}
		default :
		return state
	}
}