//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	firstname:'',
	lastname:'',
	email:'',
	age:'',
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'stepone':
		//console.log('got it')
		return {
			...state,
			firstname:action.payload.firstname,
			lastname:action.payload.lastname,
			email:action.payload.email,
			age:action.payload.age
		}
		case 'reset':
		return{
			...state,
			firstname:action.payload,
			lastname:action.payload,
			email:action.payload,
			age:action.payload
		}
		default:
		return state
	}
}