//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	sourceOfIncome:'',
	expenditureOne:'',
	expenditureTwo:'',
	expenditureThree:'',
	save:''
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'steptwo':
		//console.log('got it')
		return {
			...state,
			sourceOfIncome:action.payload.sourceOfIncome,
			expenditureOne:action.payload.expenditureOne,
			expenditureTwo:action.payload.expenditureTwo,
			expenditureThree:action.payload.expenditureThree,
			save:action.payload.save
		}
		case 'reset':
		return {
			...state,
			sourceOfIncome:action.payload,
			expenditureOne:action.payload,
			expenditureTwo:action.payload,
			expenditureThree:action.payload,
			save:action.payload
		}
		default :
		return state
	}
}