//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	savingReason:'',
	savingWay:'',
	emergencyFund:'',
	nosavingReason:'',
	inspirationToSave:''
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'stepthree':
		//console.log('got it')
		return {
			...state,
			savingReason:action.payload.savingReason,
			savingWay:action.payload.savingWay,
			emergencyFund:action.payload.emergencyFund,
			nosavingReason:action.payload.nosavingReason,
			inspirationToSave:action.payload.inspirationToSave
		}
		case 'reset':
		return {
			...state,
			savingReason:action.payload,
			savingWay:action.payload,
			emergencyFund:action.payload,
			nosavingReason:action.payload,
			inspirationToSave:action.payload
		}
		default :
		return state
	}
}